import React, { createContext, useState } from 'react';
import uuid from 'react-uuid'

export const RecipeContext = createContext();
const RecipeContextProvider = (props) => {
    const [Recipes, setRecipes] = useState([
        {
            id: 3,
            title: "Rižoto s piletinom i batatom",
            ingredients: "200 g pilećeg filea\n 1 Vegeta Natur Kokošja kocka\n 1 žlica maslaca\n 1 žlica maslinova ulja\n 1 manji luk\n 150 g batata\n 200 g Zlato polje Arborio riže\n 100 ml bijelog vina\n prstohvat Vegeta Maestro crnog papra mljevenog\n1/2 žličice Vegeta Maestro ružmarina\n40 g parmezana",
            isFav: false,
            isExpanded: false

        },
        {
            id: 1,
            title: "Mađarica",
            ingredients: "600 grama Pšeničnog oštrog brašna Podravka\n50 grama masti ili maslaca\n 150 grama šećera\n 3 cijela jaja\n 1 čašica kiselog vrhnja\n 1 vrećica Praška za pecivo Dolcela",
            isFav: false,
            isExpanded: false
        },
        {
            id: 2,
            title: "Jogurtice",
            ingredients: "300ml gustog/čvrstog/grčkog jogurta\n 80ml ulja\n 1 jaje/L\n 1,5žlica Šećera ( kristal)\n 1/2 žličice soli/manje više po ukusu\n 1/2kesice Kvasca instant Dolcela\n oko 500g Glatkog brašna Podravka",
            isFav: false,
            isExpanded: false

        },
    ]
    );

    const addRecipe = (recipeData) => {
        setRecipes([...Recipes, { id: uuid(), ...recipeData, isExpanded: true }]);
    };

    const removeRecipe = (id) => {
        setRecipes(Recipes.filter(recipe => recipe.id !== id));
    };


    const favRecipe = (id) => {
        setRecipes(Recipes.map((Recipe) => Recipe.id === id ?
            { ...Recipe, isFav: !Recipe.isFav } : Recipe))
    };


    const editRecipe = (id, recipeData) => {
        setRecipes(Recipes.map((recipe) => {
            if (recipe.id !== id) return recipe;
            return {
                id,
                ...recipe,
                ...recipeData,
            }
        }))

    };

    const editExpand = (id) => {
        setRecipes(Recipes.map((Recipe) => Recipe.id === id ?
            { ...Recipe, isExpanded: !Recipe.isExpanded } : Recipe))
    };




    return (
        <RecipeContext.Provider value={{ Recipes, addRecipe, removeRecipe, favRecipe, editRecipe, editExpand }}>
            {props.children}
        </RecipeContext.Provider>
    );
}

export default RecipeContextProvider;