import React, { useState, useContext } from 'react';
import { Modal, Button } from 'react-bootstrap';
import RecipeForm from './RecipeForm';
import { RecipeContext } from '../contexts/RecipeContext';


const EditRecipe = ({ recipe }) => {
  const { editRecipe } = useContext(RecipeContext);
  const [isEditRecipeModalVisible, setIsEditRecipeModalVisible] = useState(false);
  const onCloseModal = () => {
    setIsEditRecipeModalVisible(false);
  }
  const onShowModal = () => {
    setIsEditRecipeModalVisible(true);
  }
  const onSaveChanges = (recipeData) => {
    editRecipe(recipe.id, recipeData);
    onCloseModal();
  }

  return (
    <>
      <i className="fas fa-edit" onClick={onShowModal}></i>
      <Modal className="modal" show={isEditRecipeModalVisible} onHide={onCloseModal}>
        <Modal.Header closeButton>
          <Modal.Title>Update recipe</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <RecipeForm recipeDetails={recipe} onSave={onSaveChanges} />
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={onCloseModal}>
            Close
          </Button>
          <Button type="submit" form="recipe-form" variant="primary">
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default EditRecipe;