import React, { useState } from 'react';

const RecipeForm = ({ recipeDetails, onSave }) => {
  const [title, setTitle] = useState(recipeDetails ? recipeDetails.title : '');
  const [ingredients, setIngredients] = useState(recipeDetails ? recipeDetails.ingredients : '');
  const resetStates = () => {
    setTitle("");
    setIngredients("");
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    onSave({ title, ingredients });
    resetStates();
  }

  return (
    <form onSubmit={handleSubmit} id="recipe-form">
      <input type="text" placeholder="Recipe name" defaultValue={title} required onChange={(e) => setTitle(e.target.value)} />
      <textarea type="text" placeholder="Ingredients" required defaultValue={ingredients} onChange={(e) => setIngredients(e.target.value)} />
    </form>
  );
}

export default RecipeForm;