import React, { useState, useContext } from 'react';
import { Modal, Button } from 'react-bootstrap';
import RecipeForm from './RecipeForm';
import { RecipeContext } from '../contexts/RecipeContext';


const AddRecipe = () => {
  const { addRecipe } = useContext(RecipeContext);
  const [isAddRecipeModalVisible, setIsAddRecipeModalVisible] = useState(false);
  const onCloseModal = () => {
    setIsAddRecipeModalVisible(false);
  }
  const onShowModal = () => {
    setIsAddRecipeModalVisible(true);
  }
  const onSaveChanges = (recipeData) => {
    addRecipe(recipeData);
    onCloseModal();
  }

  return (
    <>
      <div className="button">
        <Button onClick={onShowModal}>Add new recipe</Button>
      </div>
      <Modal className="modal" show={isAddRecipeModalVisible} onHide={onCloseModal}>
        <Modal.Header closeButton>
          <Modal.Title>Add new recipe</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <RecipeForm onSave={onSaveChanges} />
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={onCloseModal}>
            Close
          </Button>
          <Button type="submit" form="recipe-form" variant="primary">
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default AddRecipe;