import React, { useState, useContext } from 'react';
import { Modal, Button } from 'react-bootstrap';
import { RecipeContext } from '../contexts/RecipeContext';


const DeleteRecipe = ({ RecipeId, RecipeName }) => {

  const { removeRecipe } = useContext(RecipeContext);
  const [IsDeleteModalVisible, setIsDeleteModalVisible] = useState(false);
  const onCloseModal = () => {
    setIsDeleteModalVisible(false);
  }

  const onShowModal = () => {
    setIsDeleteModalVisible(true);
  }


  return (
    <>
      <i className="fas fa-trash" onClick={onShowModal}></i>
      <Modal className="modal-delete" show={IsDeleteModalVisible} onHide={onCloseModal}>
        <Modal.Header closeButton>
          <Modal.Title><i className="fas fa-minus-circle"></i>Delete recipe?</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          Are you sure you want to delete "{RecipeName}" recipe?
        </Modal.Body>
        <Modal.Footer>
          <Button onClick={() => removeRecipe(RecipeId)} variant="primary">
            Delete
          </Button>
          <Button variant="secondary" onClick={onCloseModal}>
            Cancel
          </Button>
        </Modal.Footer>
      </Modal>




    </>

  );
}

export default DeleteRecipe;