import React from 'react';
import {
  BrowserRouter as Router, Switch, Route
} from 'react-router-dom';
import RecipePage from './pages/RecipePage';
import 'bootstrap/dist/css/bootstrap.min.css'; import LoginPage from './pages/LoginPage';
import RecipeContextProvider from './contexts/RecipeContext';
import UserContextProvider from './contexts/UserContext';


function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route exact path="/">
            <UserContextProvider>
              <LoginPage />
            </UserContextProvider>
          </Route>
          <Route path="/recipe">
            <UserContextProvider>
              <RecipeContextProvider>
                <RecipePage />
              </RecipeContextProvider>
            </UserContextProvider>
          </Route>
        </Switch>
      </Router>
    </div >
  );
}

export default App;
