import React, { useContext } from 'react';
import { RecipeContext } from '../contexts/RecipeContext';
import DeleteRecipe from './DeleteRecipe';
import EditRecipe from './EditRecipe';


const RecipeDetails = ({ Recipe }) => {
    const { favRecipe, editRecipeForm, editExpand } = useContext(RecipeContext);


    return (
        <li >
            <div className="title">{Recipe.title} </div>
            {Recipe.isExpanded ? (<div className="ingredients">
                <div className="show-hide-ingredients" onClick={() => { editExpand(Recipe.id) }}>Hide ingredients <i className="fas fa-angle-double-up" style={{ paddingLeft: "10px" }}></i></div>

                {Recipe.ingredients}

            </div>) : (<div className="show-hide-ingredients" onClick={() => { editExpand(Recipe.id) }}>Show ingredients <i style={{ paddingLeft: "10px" }} className="fas fa-angle-double-down"></i></div>)}
            <div className="fav-del-edit">
                <i onClick={() => favRecipe(Recipe.id)} className={Recipe.isFav ? "fas fa-star" : "far fa-star"}></i>
                <DeleteRecipe RecipeId={Recipe.id} RecipeName={Recipe.title} />
                <EditRecipe editRecipeForm={editRecipeForm} recipe={Recipe} />
            </div>
        </li>

    );
}

export default RecipeDetails;